import React, { Component } from 'react';
import DropDownList from '../ui/DropDownList';

class AddAnimal extends Component {
    
    render() {
        const renderButton = (
            <button className="btn btn-danger btn-remove" onClick={() => this.props.removeAnimalRecord(this.props.index)}>
                <i className="fe fe-x"></i>
            </button>
        );

        console.log(this.props);
        
        return (

            <div className="panel-card-div panel-card-div2">
                <div className="form-div">
                    {this.props.animalRecord.isClosed && renderButton}
                    <div className="row mr-minus-10">
                        <div className="col-lg-2 col-md-4 pr-10">
                            <DropDownList
                                placeholder="Choose Animal"
                                value={this.props.animalRecord.animal}
                                handleChange={this.props.handleChange}
                                options={this.props.dropDownOptions.animal} />
                            {this.props.animalRecord.errAnimal !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.props.animalRecord.errAnimal}</div> : null}
                        </div>
                        
                        <div className="col-lg-2 col-md-4 pr-10">
                            <DropDownList
                                placeholder="Choose Brand"
                                value={this.props.animalRecord.brand}
                                handleChange={this.props.handleChange}
                                options={this.props.dropDownOptions.brand} />
                            {this.props.animalRecord.errBrand !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.props.animalRecord.errBrand}</div> : null}
                        </div>
                        {/* <div className="col-lg-2 col-md-4 pr-10">
                            <DropDownList
                                placeholder="Choose Location"
                                options={this.props.dropDownOptions.location}
                                value={this.props.animalRecord.location}
                                handleChange={this.props.handleChange}
                            />
                            {this.props.animalRecord.errLocation !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.props.animalRecord.errLocation}</div> : null}
                        </div> */}
                        {this.props.animalRecord.showColor && <div className="col-lg-2 col-md-4 pr-10">
                            <DropDownList
                                placeholder="Choose color"
                                value={this.props.animalRecord.color}
                                handleChange={this.props.handleChange}
                                options={this.props.dropDownOptions.color} />
                            {this.props.animalRecord.errcolor !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.props.animalRecord.errcolor}</div> : null}
                        </div>}
                        {this.props.animalRecord.showBreed && <div className="col-lg-2 col-md-4 pr-10">
                            <DropDownList
                                placeholder="Choose Breed"
                                value={this.props.animalRecord.breed}
                                handleChange={this.props.handleChange}
                                options={this.props.dropDownOptions.breed} />
                            {this.props.animalRecord.errBreed !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.props.animalRecord.errBreed}</div> : null}
                        </div>
                        }
                        <div className="col-lg-4 col-md-4 pr-10">
                            <div className="form-group input-box-effect">
                                <label className="input-box-label">
                                    <input
                                        type="number"
                                        className="form-input"
                                        value={this.props.animalRecord.quantity}
                                        name="quantity"
                                        onChange={(e) => this.props.onInputChangeHandler(e, this.props.animalRecord)}
                                        placeholder="&nbsp;"
                                        min={0}
                                        disabled={false} />
                                    <span className="label">Enter Quantity</span>
                                    <span className="border"></span>
                                </label>
                                {this.props.animalRecord.errQuantity !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '5px 0px 15px 0px' }}>{this.props.animalRecord.errQuantity}</div> : null}
                            </div>

                        </div>
                    </div>
                </div>
            </div>
        );
    }
}
export default AddAnimal;