import axios from "axios";

const services = axios.create({
    headers: { 
        "Content-Type": "application/json" 
    }
    
});


const servicesForData = axios.create({
    headers: {
        "Content-Type": "multipart/form-data",
    }
    
});
// const tokenHeaders = (useToken) => {
//     const jwt = localStorage.getItem("jwt");
//     const headers = useToken && jwt ? { headers: { token: jwt } } : {};
//     return headers;
// };

export const get = (url, useToken = true) => {
    //const headers = tokenHeaders(useToken);
    return services.get(url);
};

export const post = (url, data, useToken = true) => {
    //const headers = tokenHeaders(useToken);
    return services.post(url, data);
};

export const postFormData = (url, data, useToken = true) => {
    //const headers = tokenHeaders(useToken);
    return servicesForData.post(url, data);
};

export const putFormData = (url, data, useToken = true) => {
    //const headers = tokenHeaders(useToken);
    return servicesForData.put(url, data);
};

export const put = (url, data, useToken = true) => {
    //const headers = tokenHeaders(useToken);
    return services.put(url, data);
};

export const remove = (url, data, useToken = true) => {
    //const headers = tokenHeaders(useToken);
    return services.delete(url, data);
  };
  