const getBaseUrl = () => {
    const dev = process.env.NODE_ENV === 'development';
  
    if (dev) {
      //here make base url 
      return 'http://15.206.11.82:8080';
    } else {
      return 'http://15.206.11.82:8080';
    }
};


export const getUrl = (type) => {
    const baseUrl = getBaseUrl();

    switch (type) {
      case 'stockman_init':
        return `${baseUrl}/api/v1/common/stockman/init`;
      case 'get_animal_init':
        return `${baseUrl}/api/v1/common/animal/init`;
      case 'save_live_stock_step_1':
        return `${baseUrl}/api/v1/save_live_stock/step-1/`;
      case 'save_live_stock_step_2':
        return `${baseUrl}/api/v1/save_live_stock/step-2/`;
      case 'get_brand_dealer':
        return `${baseUrl}/api/v1/common/brand_dealer/`;
      case 'update_live_stock_step_1':
        return `${baseUrl}/api/v1/save_live_stock/step-1`
      case 'update_live_stock_step_2':
        return `${baseUrl}/api/v1/save_live_stock`
      case 'update_live_stock_step_3':
        return `${baseUrl}/api/v1/save_live_stock`
      case 'horse_permanent_travel_permit':
        return `${baseUrl}/api/v1/travel_permit/`;
      case 'add_brand_dealer':
        return `${baseUrl}/utad/add_brand_dealer.php`
      case 'update_horse_permanent_travel_permit':
        return `${baseUrl}/api/v1/travel_permit/`;
      case 'print_horse_permanent_travel_permit':
        return `${baseUrl}/api/v1/travel_permit/print`;
      case 'get_live_stock_detail':
        return `${baseUrl}/api/v1/save_live_stock/print`;
      case 'get_live_stock_list':
        return `${baseUrl}/api/v1/save_live_stock/list-inspection`;
      default:
        return baseUrl;
    }
  };
  