import React from 'react';

const InputField  = ({ placeholder,value,onChangeMethod,name,disabled, handleBlur})=> (
    <div className="form-group input-box-effect">
      <label className="input-box-label">
        <input 
            type="text" 
            autoComplete={'off'}
            className="form-input" 
            value={value}
            name={name}
            onChange={onChangeMethod}
            placeholder="&nbsp;"
            disabled = {disabled}
            onBlur={handleBlur}
             />
            <span className="label">{placeholder}</span>
            <span className="border"></span>
        </label>
     
    </div>
)
export default InputField