import React, { Component } from 'react';

class  StepInddicator extends Component {
    constructor() {
        super();
        this.state = {
          // Completed - to add a check mark
          // Selected - to fill step with color
          // Highlighted - to make text of selected step bold
          steps: []
        };
      }

    onchanges=()=>{
        
    }
    componentDidMount() {
        const { steps, currentStepNumber } = this.props;
        const stepsState = steps.map((step, index) => {
          const stepObj = {};
          stepObj.description = step;
          stepObj.highlighted = index === 0 ? true : false;
          stepObj.selected = index === 0 ? true : false;
          stepObj.completed = false;
          return stepObj;
        });
        const currentSteps = this.updateStep(currentStepNumber, stepsState);
    
        this.setState({
        steps: currentSteps
        });
    }
    componentDidUpdate(prevProps) {
        const { steps } = this.state;
        const currentSteps = this.updateStep(this.props.currentStepNumber, steps);
    
        if (prevProps.currentStepNumber !== this.props.currentStepNumber)
          this.setState({
            steps: currentSteps
          });
    }
    updateStep(stepNumber, steps) {
        const newSteps = [...steps];
        let stepCounter = 0;
    
        // Selected - to fill step with color
        // Highlighted - to make text of selected step bold
    
        while (stepCounter < newSteps.length) {
          // Current step
          if (stepCounter === stepNumber) {
            newSteps[stepCounter] = {
              ...newSteps[stepCounter],
              highlighted: true,
              selected: true,
            };
            stepCounter++;
          }
          // Past step
          else if (stepCounter < stepNumber) {
            newSteps[stepCounter] = {
              ...newSteps[stepCounter],
              highlighted: false,
              selected: true,
            };
            stepCounter++;
          }
          // Future step
          else {
            newSteps[stepCounter] = {
              ...newSteps[stepCounter],
              highlighted: false,
              selected: false,
            };
            stepCounter++;
          }
        }
    
        return newSteps;
      }
    
    render () { 
        const { steps } = this.state;
        const stepsJSX = steps.map((step, index) => {
            return (
                <li className={step.selected ? "active" : ""} key ={index} >
                  <label className={step.highlighted ? "active" : ""}  >
                      <span className="round-number">{index+1}</span>
                      <span className="title-text">{step.description}</span>
                  </label>
                </li> 
            );
          });
          return (
            <div className="step-forms-nav-tab">
                <ul className="nav nav-tabs">
                    {stepsJSX}
                </ul>
            </div> 
        );
    }
} 
export default StepInddicator;