import React, { Component } from 'react';
import "flatpickr/dist/themes/material_blue.css";
import Flatpickr from "react-flatpickr";
import _ from 'lodash';
import { getUrl } from '../../services/network/urls';
import {  get ,post,put} from '../../services/network/requests';
import InputField from '../ui/InputField';
import DropDownList from '../ui/DropDownList'
// import {autocompleteResponse} from "../../staticData/mocks";
class LiveStockStep1 extends Component {
    state = {
        ownerName: this.props.liveStockStep1Data.owner_name !== '' ? this.props.liveStockStep1Data.owner_name : '',
        ownerAddress: this.props.liveStockStep1Data.owner_street_address !== '' ? this.props.liveStockStep1Data.owner_street_address: '',
        ownerState: this.props.liveStockStep1Data.ownerState !== '' ? this.props.liveStockStep1Data.ownerState : '',
        ownerCity: this.props.liveStockStep1Data.owner_city !== '' ? this.props.liveStockStep1Data.owner_city : '',
        ownerCounty: this.props.liveStockStep1Data.ownerCounty  !== '' ? this.props.liveStockStep1Data.ownerCounty  : '',
        ownerZipCode: this.props.liveStockStep1Data.owner_zip_code !== '' ? this.props.liveStockStep1Data.owner_zip_code : '',
        buyerName: this.props.liveStockStep1Data.buyer_name !== '' ? this.props.liveStockStep1Data.buyer_name : '',
        buyerAddress: this.props.liveStockStep1Data.buyer_street_address !== '' ? this.props.liveStockStep1Data.buyer_street_address : '',
        buyerState: this.props.liveStockStep1Data.buyerState !== '' ? this.props.liveStockStep1Data.buyerState : '',
        buyerCity: this.props.liveStockStep1Data.buyer_city !== '' ? this.props.liveStockStep1Data.buyer_city : '',
        buyerCounty: this.props.liveStockStep1Data.buyerCounty !== '' ? this.props.liveStockStep1Data.buyerCounty : '',
        buyerZipCode: this.props.liveStockStep1Data.buyer_zip_code !== '' ? this.props.liveStockStep1Data.buyer_zip_code : '',
        soldTo: this.props.liveStockStep1Data.sold_to !== '' ? this.props.liveStockStep1Data.sold_to : '',
        consignedTo: this.props.liveStockStep1Data.consigned_to !== '' ? this.props.liveStockStep1Data.consigned_to : '',
        destination: this.props.liveStockStep1Data.destination !== '' ? this.props.liveStockStep1Data.destination : '',
        carrier: this.props.liveStockStep1Data.carrier !== '' ? this.props.liveStockStep1Data.carrier : '',
        licNo: this.props.liveStockStep1Data.lic_no !== '' ? this.props.liveStockStep1Data.lic_no : '',
        ownerNameErr: '',
        ownerAddressErr: '',
        ownerCityErr: '',
        ownerStateErr: '',
        ownerCountyErr: '',
        ownerZipCodeErr: '',
        buyerNameErr: '',
        buyerAddressErr: '',
        buyerCityErr: '',
        buyerStateErr: '',
        buyerCountyErr: '',
        buyerZipCodeErr: '',
        soldToErr: '',
        consignedToErr: '',
        destinationErr: '',
        carrierErr: '',
        licNoErr:'',
        date: this.props.liveStockStep1Data.date !== '' ? this.props.liveStockStep1Data.date : new Date(),
        filteredSuggestions: [],
        filteredSuggestionsBuyer: [],
        stateOptions :[],
        ownerCountyOptions: [],
        buyerCountyOptions: [],
        countyOptionsOriginal :[],
        defualtCountyOptions:[],
        dataObject : {}
    }
    
    async componentDidMount() {
        await this.getAutoCompleteData();
        if(this.props.match.params.id){
            await this.getLiveStockDetail();
        }
    }

    getLiveStockDetail = () => {
        const url = getUrl("get_live_stock_detail");
        get(`${url}/${this.props.match.params.id}/`)
            .then(res => {
                const { data: { code, data, status } } = res;
                switch (code) {
                    case 200:
                        if (status) {
                            console.log(data);
                            let ownerState = this.state.stateOptions.filter((e) => e.label === data.owner_state);
                            let buyerState = this.state.stateOptions.filter((e) => e.label === data.buyer_state);

                            let ownerCounty = this.state.countyOptionsOriginal.filter((e) => e.label === data.owner_county);
                            if (ownerState.label !== "UTAH") {
                                this.setState({
                                    ownerCountyOptions: this.state.defualtCountyOptions,

                                })
                                ownerCounty = this.state.defualtCountyOptions;
                            }
                            let buyerCounty = this.state.countyOptionsOriginal.filter((e) => e.label === data.buyer_county);
                            if (buyerState.label !== "UTAH") {
                                this.setState({
                                    buyerCountyOptions: this.state.defualtCountyOptions,

                                })
                                buyerCounty = this.state.defualtCountyOptions;
                            }
                            this.setState({
                                
                                ownerName: data.owner_name !== '' ? data.owner_name : '',
                                ownerAddress: data.owner_street_address !== '' ? data.owner_street_address : '',
                                ownerCity: data.owner_city !== '' ? data.owner_city : '',
                                ownerState: ownerState[0],
                                ownerCounty: ownerCounty[0],
                                ownerZipCode: data.owner_zip_code !== '' ? data.owner_zip_code : '',
                                buyerName: data.buyer_name !== '' ? data.buyer_name : '',
                                buyerAddress: data.buyer_street_address !== '' ? data.buyer_street_address : '',
                                buyerState: buyerState[0],
                                buyerCounty: buyerCounty[0],
                                buyerCity: data.buyer_city !== '' ? data.buyer_city : '',
                                buyerZipCode: data.buyer_zip_code !== '' ? data.buyer_zip_code : '',
                                soldTo: data.sold_to !== '' ? data.sold_to : '',
                                consignedTo: data.consigned_to !== '' ? data.consigned_to : '',
                                destination: data.destination !== '' ? data.destination : '',
                                carrier: data.carrier !== '' ? data.carrier : '',
                                licNo: data.lic_no !== '' ? data.lic_no : '',
                                date: data.date !== '' ? data.date : '',
                            },() => {
                                let inspectedFor = [];
                                if (data.live_stock.change_to_ownership){
                                    inspectedFor.push('change_to_ownership')
                                }
                                if (data.live_stock.life_time_travel_permit){
                                    inspectedFor.push('life_time_travel_permit')
                                }
                                if (data.live_stock.other){
                                    inspectedFor.push('other')
                                }
                                if (data.live_stock.slaughter){
                                    inspectedFor.push('slaughter')
                                }
                                if (data.live_stock.yearly_travel_permit){
                                    inspectedFor.push('yearly_travel_permit')
                                }
                                if (data.live_stock.released_at_no_charge) {
                                    inspectedFor.push('released_at_no_charge')
                                }
                                if (data.live_stock.leaving_the_state) {
                                    inspectedFor.push('leaving_the_state')
                                }
                                let secondStepData = {
                                    Owner_permises_no: data.live_stock.Owner_permises_no,
                                    addAnimalRow: data.aminal_row,
                                    dealerId: data.live_stock.dealer_license_no.id,
                                    dealers_license_no: data.live_stock.dealer_license_no.lic_no,
                                    latitude: data.live_stock.latitude,
                                    longitude: data.live_stock.longitude,
                                    id: data.live_stock.id !== undefined ? data.live_stock.id : null,
                                    inspectedFor: inspectedFor,
                                    gps_coordinates : '',
                                    owner_information: data.live_stock.owner_information,
                                    qr_code: "",
                                    specify: data.live_stock.specify,
                                    total_animal: data.live_stock.total_animal,
                                    payment_by: data.live_stock.payment_by
                                }
                               
                                    this.props.handleLiveStockStep2(secondStepData);
                            })
                            //this.props.handleLiveStockStep1(data);
                            //this.props
                        }
                        break;

                    default:
                        break;
                }
            })
            .catch(error => console.log(error))
    }

    getAutoCompleteData = () => {
        const url = getUrl("stockman_init");
        get(url)
        .then(res => {
            const { data: { code, data,  status } } = res;
            switch (code) {
                case 200:
                    if (status) {
                        const stateOption = data.state.map(({id:value,state:label})=>({value,label}))
                        const countyOption = data.county.map(({id:value,county:label})=>({value,label}))
                        let defualtCountyOptions = countyOption.filter((e) => e.label === 'Out-of-State' );
                        this.setState({ 
                            ownerData: data.owner, 
                            buyerData: data.buyer,
                            stateOptions: stateOption,
                            ownerCountyOptions:countyOption,
                            buyerCountyOptions:countyOption,
                            countyOptionsOriginal:countyOption,
                            defualtCountyOptions: defualtCountyOptions
                        })
                    }
                    break;
    
                default:
                    break;
            }
        })
        .catch(error => console.log(error))
    }

    onInputChangeHandler = (e) => {
        const { name, value } = e.target
        this.setState({ [name]: value }, () => {
            switch (name) {
                case 'ownerName':
                    if (value === '') {
                        this.setState({ ownerNameErr: "Owner name is requied", activeSuggestionOwner: false })
                    } else {
                        const filteredSuggestions = this.state.ownerData.filter(
                            suggestion =>
                                suggestion.name.toLowerCase().indexOf(this.state.ownerName.toLowerCase()) > -1
                        );
                        this.setState({ ownerNameErr: '', activeSuggestionOwner: true, filteredSuggestions, activeSuggestionBuyer: false })
                    }
                    break;
                case 'ownerAddress':
                    if (value === '') {
                        this.setState({ ownerAddressErr: "Owner Address is requied" })
                    } else {
                        this.setState({ ownerAddressErr: '', activeSuggestionOwner: false, activeSuggestionBuyer: false })
                    }
                    break;
                case 'buyerName':
                    if (value === '') {
                        this.setState({ buyerNameErr: "Buyer name is requied", activeSuggestionBuyer: false })
                    } else {
                        const filteredSuggestionsBuyer = this.state.buyerData.filter(
                            suggestion =>
                                suggestion.name.toLowerCase().indexOf(this.state.buyerName.toLowerCase()) > -1
                        );
                        this.setState({ buyerNameErr: '', activeSuggestionBuyer: true, activeSuggestionOwner: false, filteredSuggestionsBuyer })
                    }
                    break;
                case 'buyerAddress':
                    if (value === '') {
                        this.setState({ buyerAddressErr: "Buyer Address is requied" })
                    } else {
                        this.setState({ buyerAddressErr: '', activeSuggestionOwner: false, activeSuggestionBuyer: false })
                    }
                    break;
                case 'ownerCity': 
                case 'ownerState': 
                case 'ownerCounty': 
                case 'ownerZipCode': 
                case 'buyerCity': 
                case 'buyerState': 
                case 'buyerCounty': 
                case 'buyerZipCode': 
                    if (value === '') {
                        this.setState({ [name + 'Err']: _.startCase(name) + " is requied" })
                        
                    } else {
                        this.setState({ [name + 'Err']: '' })
                        
                    }
                    break;
                case 'soldTo':
                    if (value === '') {
                        this.setState({ soldToErr: "Sold to is requied" })
                    } else {
                        this.setState({ soldToErr: '', activeSuggestionOwner: false, activeSuggestionBuyer: false })
                    }
                    break;
                case 'consignedTo':
                    if (value === '') {
                        this.setState({ consignedToErr: "Consigned to is requied" })
                    } else {
                        this.setState({ consignedToErr: '', activeSuggestionOwner: false, activeSuggestionBuyer: false })
                    }
                    break;
                case 'destination':
                    if (value === '') {
                        this.setState({ destinationErr: "Destination is requied" })
                    } else {
                        this.setState({ destinationErr: '', activeSuggestionOwner: false, activeSuggestionBuyer: false })
                    }
                    break;
                case 'carrier':
                    if (value === '') {
                        this.setState({ carrierErr: "Carrier is requied" })
                    } else {
                        this.setState({ carrierErr: '', activeSuggestionOwner: false, activeSuggestionBuyer: false })
                    }
                    break;
                case 'licNo':
                    if (value === '') {
                        this.setState({ licNoErr: "Lic.No. is requied" })
                    } else {
                        this.setState({ licNoErr: '', activeSuggestionOwner: false, activeSuggestionBuyer: false })
                    }
                    break;
                default:
                    break;
            }
        })
    }

    isFormValid = () => {
        const {
            ownerAddress,
            ownerName,
            buyerName,
            buyerAddress,
            soldTo,
            consignedTo,
            destination,
            licNo,
            carrier,
            ownerCity,
            ownerState,
            ownerCounty,
            ownerZipCode,
            buyerCity,
            buyerState,
            buyerCounty,
            buyerZipCode,
        } = this.state;
        let isValid = true;
        if (ownerName === '' || ownerName === undefined) {
            isValid = false;
            this.setState({ ownerNameErr: "Owner name is requied" })
        }
        if (ownerAddress === '' || ownerAddress === undefined) {
            isValid = false;
            this.setState({ ownerAddressErr: "Owner Address is requied" })
        }
        if (buyerName === '' || buyerName === undefined) {
            isValid = false;
            this.setState({ buyerNameErr: "Buyer name is requied" })
        }
        if (buyerAddress === '' || buyerAddress === undefined) {
            isValid = false;
            this.setState({ buyerAddressErr: "Buyer Address is requied" })
        }
        if (soldTo === '' || soldTo === undefined) {
            isValid = false;
            this.setState({ soldToErr: "Sold to is requied" })
        }
        if (consignedTo === '' || consignedTo === undefined) {
            isValid = false;
            this.setState({ consignedToErr: "Consigned to is requied" })
        }
        if (destination === '' || destination === undefined) {
            isValid = false;
            this.setState({ destinationErr: "Destination is requied" })
        }
        if (carrier === '' || carrier === undefined) {
            isValid = false;
            this.setState({ carrierErr: "Carrier is requied" })
        }
        if(licNo === '' || licNo === undefined) {
            isValid = false;
            this.setState({ licNoErr: "Lic.No. is requied" })
        }
        if (ownerCity   === '' || ownerCity === undefined) {isValid = false;this.setState({ ownerCityErr: "Owner City is requied" })}
        if (ownerState  === '' || ownerState === undefined) {isValid = false;this.setState({ ownerStateErr: "Owner State is requied" })}
        if (ownerCounty === '' || ownerCounty === undefined) {isValid = false;this.setState({ ownerCountyErr: "Owner County is requied" })}
        if (ownerZipCode=== '' || ownerZipCode === undefined) {isValid = false;this.setState({ ownerZipCodeErr: "Owner Zip Code is requied" })}
        if (buyerCity   === '' || buyerCity === undefined) {isValid = false;this.setState({ buyerCityErr: "Buyer City is requied" })}
        if (buyerState  === '' || buyerState === undefined) {isValid = false;this.setState({ buyerStateErr: "Buyer State is requied" })}
        if (buyerCounty === '' || buyerCounty === undefined) { isValid = false; this.setState({ buyerCountyErr: "Buyer County is requied" }) }
        if (buyerZipCode=== '' || buyerZipCode === undefined) { isValid = false; this.setState({buyerZipCodeErr: "Buyer Zip Code is requied" }) }
        return isValid;
    }

    handleCheckForm = () => {
        const isValid = this.isFormValid();
        if (isValid) this.submitForm();
    }

    submitForm = () => {
        
        const data = {
            owner_name: this.state.ownerName,
            owner_street_address: this.state.ownerAddress,
            owner_city: this.state.ownerCity,
            owner_state : this.state.ownerState.label,
            owner_zip_code: this.state.ownerZipCode,
            owner_county: this.state.ownerCounty.label,
            buyer_name: this.state.buyerName,
            buyer_street_address: this.state.buyerAddress,
            buyer_city: this.state.buyerCity,
            buyer_state : this.state.buyerState.label,
            buyer_zip_code: this.state.buyerZipCode,
            buyer_county: this.state.buyerCounty.label,
            sold_to: this.state.soldTo,
            consigned_to:this.state.consignedTo,
            carrier: this.state.carrier,
            destination: this.state.destination,
            lic_no: this.state.licNo,
           
        }
        
        if (this.props.liveStockStep1Data.id === null && this.props.match.params.id === undefined) {
            const url = getUrl("save_live_stock_step_1")
            post(url,data)
            .then((res)=>{
                const { data: { code, data,  status } } = res;
                switch (code) {
                    case 200:
                        if (status) {
                            data.buyer_street_address = this.state.buyerAddress
                            data.ownerState = this.state.ownerState
                            data.ownerCounty = this.state.ownerCounty
                            data.buyerState = this.state.buyerState
                            data.buyerCounty = this.state.buyerCounty
                            this.props.handleLiveStockStep1(data)
                            const currentStep = 2
                            this.props.handleSetStep(currentStep);
                        }
                        break;
                    default:
                        break;
                }
            })
            .catch((err)=>{
                console.log("error",err)
            })
        }else {
            let id = 0;
            if (this.props.liveStockStep1Data.id !== null){
                id = this.props.liveStockStep1Data.id
            }else{
                id = this.props.match.params.id
            }
            
            
            const url = getUrl("update_live_stock_step_1")
            put( `${url}/${id}/`,data)
            .then((res)=>{
                const { data: { code, data,  status } } = res;
                switch (code) {
                    case 200:
                        if (status) {
                            data.ownerState = this.state.ownerState
                            data.ownerCounty = this.state.ownerCounty
                            data.buyerState = this.state.buyerState
                            data.buyerCounty = this.state.buyerCounty
                            this.props.handleLiveStockStep1(data)
                            const currentStep = 2

                            this.props.handleSetStep(currentStep);
                        }
                        break;
                    default:
                        break;
                }
            })
            .catch((err)=>{
                console.log(err)
            })
        }   
    }

    handleAutoComplete = (name, address,data) => {
        const ownerState = this.state.stateOptions.find(option => option.value === data.state)
        let ownerCounty = this.state.ownerCountyOptions.find(option => option.value === data.county)
        // console.log(ownerState);
        if(ownerState.label !== "UTAH"){
            this.setState({
                ownerCountyOptions: this.state.defualtCountyOptions,
            })
            ownerCounty = this.state.defualtCountyOptions[0];
        }
        
        this.setState({
            
            ownerName: data.name, 
            ownerAddress: data.street_address,
            ownerCity:data.city,
            ownerState:ownerState,
            ownerCounty:ownerCounty,
            ownerZipCode:data.zip_code,
            activeSuggestionOwner: false, 
            ownerNameErr: '', 
            ownerAddressErr: '',
            ownerCityErr:'',
            ownerStateErr:'',
            ownerCountyErr:'' ,
            ownerZipCodeErr:''
        })
    }

    handleBuyerAutoComplete = (name, address,data) => {
        const buyerState = this.state.stateOptions.find(option => option.value === data.state)
        
        let buyerCounty = this.state.buyerCountyOptions.find(option => option.value === data.county)
        if (buyerState.label !== "UTAH") {
            this.setState({
                buyerCountyOptions: this.state.defualtCountyOptions,
                
            })
            buyerCounty = this.state.defualtCountyOptions[0];
        }
        
        this.setState({
            buyerId:data.id,  
            buyerName: name, 
            buyerAddress:data.street_address,
            buyerCity:data.city,
            buyerState:buyerState,
            buyerCounty:buyerCounty,
            buyerZipCode:data.zip_code, 
            soldTo: data.sold_to !== '' ? data.sold_to : '',
            consignedTo: data.consigned_to !== '' ? data.consigned_to : '',
            destination: data.destination !== '' ? data.destination : '',
            carrier: data.carrier !== '' ? data.carrier : '',
            activeSuggestionBuyer: false, 
            buyerNameErr: '', 
            buyerAddressErr: '' ,
            buyerCityErr:'',
            buyerStateErr:'',
            buyerCountyErr:'' ,
            buyerZipCodeErr:''
        })
    }
    handleChangeOwnerState = (SelectedOption) => {
        
        
        this.setState({ownerState:SelectedOption,ownerStateErr:''},() => {
            if (SelectedOption.label !== 'UTAH'){
                this.setState({
                    ownerCountyOptions: this.state.defualtCountyOptions
                })
            }else{
                this.setState({
                    ownerCountyOptions: this.state.countyOptionsOriginal
                })
            }
        })
    }
    handleChangeOwnerCounty = (SelectedOption) => {
        this.setState({ownerCounty:SelectedOption,ownerCountyErr:''})
    }
    handleChangeBuyerState = (SelectedOption) => {
        this.setState({buyerState:SelectedOption,buyerStateErr:''},() => {
            if (SelectedOption.label !== 'UTAH') {
                this.setState({
                    buyerCountyOptions: this.state.defualtCountyOptions
                })
            } else {
                this.setState({
                    buyerCountyOptions: this.state.countyOptionsOriginal
                })
            }
        })
        
    }
    handleChangeBuyerCounty = (SelectedOption) => {
        this.setState({buyerCounty:SelectedOption,buyerCountyErr:''})
    }
    
    render() {
        
        
        return (
            <div id="menu1" className="tab-pane tab-pane-one fade active show">
                <div className="panel-card-div pb-6">
                    <div className="form-div">
                        <div className="row mr-minus-10">
                            <div className="col-lg-3 col-md-4 pr-10">
                                <InputField
                                    placeholder="Owner Name"
                                    value={this.state.ownerName}
                                    name="ownerName"
                                    onChangeMethod={this.onInputChangeHandler}
                                />
                                {this.state.activeSuggestionOwner === true && this.state.ownerName.length >= 1 && this.state.filteredSuggestions.length > 0 &&
                                    <ul className="typeahead dropdown-menu" style={{ top: "46px", left: "10px", width: "221.328px", display: "block" }}>
                                        {
                                            this.state.filteredSuggestions.map((data, index) => {
                                                return <li key={index} name="ownerName" onClick={() => this.handleAutoComplete(data.name, data.street_address,data)}>
                                                    <label>{data.name}</label>
                                                    <label className="address-link" >{data.street_address}</label>
                                                </li>
                                            }) 
                                        }
                                    </ul>
                                }
                                {this.state.ownerNameErr !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.ownerNameErr}</div> : null}
                            </div>
                            <div className="col-lg-3 col-md-4 pr-10">
                                <InputField
                                    placeholder="Street Address"
                                    value={this.state.ownerAddress}
                                    name="ownerAddress"
                                    onChangeMethod={this.onInputChangeHandler} />
                                {this.state.ownerAddressErr !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.ownerAddressErr}</div> : null}
                            </div>
                            <div className="col-lg-3 col-md-4 pr-10">
                                <InputField
                                    placeholder="City"
                                    value={this.state.ownerCity}
                                    name="ownerCity"
                                    onChangeMethod={this.onInputChangeHandler} />
                                {this.state.ownerCityErr !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.ownerCityErr}</div> : null}
                            </div>
                            <div className="col-lg-3 col-md-4 pr-10">
                                {/* <InputField
                                    placeholder="State"
                                    value={this.state.ownerState}
                                    name="ownerState"
                                    onChangeMethod={this.onInputChangeHandler} /> */}
                                <DropDownList 
                                    placeholder="Choose State"
                                    value={this.state.ownerState}
                                    handleChange={this.handleChangeOwnerState}
                                    options={this.state.stateOptions}
                                />
                                {this.state.ownerStateErr !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.ownerStateErr}</div> : null}
                            </div>
                            <div className="col-lg-3 col-md-4 pr-10">
                                <DropDownList 
                                    placeholder="Choose County"
                                    value={this.state.ownerCounty}
                                    handleChange={this.handleChangeOwnerCounty}
                                    options={this.state.ownerCountyOptions}
                                />
                                {/* <InputField
                                    placeholder="County"
                                    value={this.state.ownerCounty}
                                    name="ownerCounty"
                                    onChangeMethod={this.onInputChangeHandler} /> */}
                                {this.state.ownerCountyErr !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.ownerCountyErr}</div> : null}
                            </div>
                            <div className="col-lg-3 col-md-4 pr-10">
                                <InputField
                                    placeholder="Zip Code"
                                    value={this.state.ownerZipCode}
                                    name="ownerZipCode"
                                    onChangeMethod={this.onInputChangeHandler} />
                                {this.state.ownerZipCodeErr !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.ownerZipCodeErr}</div> : null}
                            </div>
                        </div>
                        <div className="row mr-minus-10">
                            <div className="col-lg-3 col-md-4 pr-10">
                                <InputField
                                    placeholder="Buyer Name"
                                    value={this.state.buyerName}
                                    name="buyerName"
                                    onChangeMethod = {this.onInputChangeHandler}/>
                                {this.state.activeSuggestionBuyer === true && this.state.buyerName.length >= 1 && this.state.filteredSuggestionsBuyer.length > 0  &&
                                    <ul className="typeahead dropdown-menu" style={{ top: "46px", left: "10px", width: "221.328px", display: "block" }}>
                                        {
                                            this.state.filteredSuggestionsBuyer.map((data, index) => {
                                                return <li key={index} name="buyername" onClick={() => this.handleBuyerAutoComplete(data.name, data.street_address,data)}>
                                                    <label>{data.name}</label>
                                                    <label className="address-link">{data.street_address}</label>
                                                </li>
                                            }) 
                                        }
                                    </ul>
                                }
                                {this.state.buyerNameErr !== ""?<div style={{fontSize:"14px",color:"red",margin:'-15px 0px 15px 0px'}}>{this.state.buyerNameErr}</div>:null}   
                            </div>
                            <div className="col-lg-3 col-md-4 pr-10">
                                <InputField
                                    placeholder="Street Address"
                                    value={this.state.buyerAddress}
                                    name="buyerAddress"
                                    onChangeMethod={this.onInputChangeHandler} />
                                {this.state.buyerAddressErr !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.buyerAddressErr}</div> : null}
                            </div>
                            <div className="col-lg-3 col-md-4 pr-10">
                                <InputField
                                    placeholder="City"
                                    value={this.state.buyerCity}
                                    name="buyerCity"
                                    onChangeMethod={this.onInputChangeHandler} />
                                {this.state.buyerCityErr !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.buyerCityErr}</div> : null}
                            </div>
                            <div className="col-lg-3 col-md-4 pr-10">
                                {/* <InputField
                                    placeholder="State"
                                    value={this.state.buyerState}
                                    name="buyerState"
                                    onChangeMethod={this.onInputChangeHandler} /> */}
                                <DropDownList 
                                    placeholder="Choose State"
                                    value={this.state.buyerState}
                                    handleChange={this.handleChangeBuyerState}
                                    options={this.state.stateOptions}
                                />
                                {this.state.buyerStateErr !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.buyerStateErr}</div> : null}
                            </div>
                            <div className="col-lg-3 col-md-4 pr-10">
                                {/* <InputField
                                    placeholder="County"
                                    value={this.state.buyerCounty}
                                    name="buyerCounty"
                                    onChangeMethod={this.onInputChangeHandler} /> */}
                                <DropDownList 
                                    placeholder="Choose County"
                                    value={this.state.buyerCounty}
                                    handleChange={this.handleChangeBuyerCounty}
                                    options={this.state.buyerCountyOptions}
                                />
                                {this.state.buyerCountyErr !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.buyerCountyErr}</div> : null}
                            </div>
                            <div className="col-lg-3 col-md-4 pr-10">
                                <InputField
                                    placeholder="Zip Code"
                                    value={this.state.buyerZipCode}
                                    name="buyerZipCode"
                                    onChangeMethod={this.onInputChangeHandler} />
                                {this.state.buyerZipCodeErr !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.buyerZipCodeErr}</div> : null}
                            </div>
                        </div>
                        <div className="row mr-minus-10">
                                
                            
                            <div className="col-lg-3 col-md-4 pr-10">
                                <InputField
                                    placeholder="Sold to"
                                    value={this.state.soldTo}
                                    name="soldTo"
                                    onChangeMethod={this.onInputChangeHandler} />
                                {this.state.soldToErr !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.soldToErr}</div> : null}
                            </div>
                            <div className="col-lg-3 col-md-4 pr-10">
                                <InputField
                                    placeholder="Consigned to"
                                    value={this.state.consignedTo}
                                    name="consignedTo"
                                    onChangeMethod={this.onInputChangeHandler} />
                                {this.state.consignedToErr !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.consignedToErr}</div> : null}
                            </div>
                            <div className="col-lg-3 col-md-4 pr-10">
                                <InputField
                                    placeholder="Destination"
                                    value={this.state.destination}
                                    name="destination"
                                    onChangeMethod={this.onInputChangeHandler} />
                                {this.state.destinationErr !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.destinationErr}</div> : null}
                            </div>
                            <div className="col-lg-3 col-md-4 pr-10">
                                <InputField
                                    placeholder="Carrier"
                                    value={this.state.carrier}
                                    name="carrier"
                                    onChangeMethod={this.onInputChangeHandler} />
                                {this.state.carrierErr !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.carrierErr}</div> : null}
                            </div>
                            <div className="col-lg-3 col-md-4 pr-10">
                                <InputField
                                    placeholder="LIC.NO."
                                    value={this.state.licNo}
                                    name="licNo"
                                    onChangeMethod={this.onInputChangeHandler} />
                                {this.state.licNoErr !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.licNoErr}</div> : null}
                            </div>
                            <div className="col-lg-3 col-md-4 pr-10">
                                <div className="form-group">
                                    <div className="input-group datepicker-form">
                                        <Flatpickr
                                            options={{
                                                enableTime: false,
                                                altInput: true,
                                                altFormat: "F j, Y",
                                                dateFormat: "Y-m-d",
                                            }}
                                            onChange={date => {
                                                this.setState({ date });
                                            }}
                                            placeholder="Select Date"
                                            value={this.state.date}
                                        />
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div className="tab-footer">
                    <div className="row ">
                        <div className="col-lg-12 justify-content-end">
                            <div className="right-side-footer d-flex justify-content-end">
                                <div className="step-btn width-25 sm-width-100" onClick={this.handleCheckForm}>
                                    <label className="btn btn-primary btn-next">Next</label>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

            </div>
        );
    }
}

export default LiveStockStep1;