import { connect } from 'react-redux';
import { withRouter } from 'react-router';
import LiveStockStep2 from '../components/LiveStock/LiveStockStep2';
import {setLiveStockStep2,setStep} from '../actions/liveStockAction';

const mapStateToProps = (state) => ({
    liveStockStep1Data:state.livestock.liveStockStep1,
    liveStockStep2Data : state.livestock.liveStockStep2,
});
    
const mapDispatchToProps = (dispatch) => ({
    handleLiveStockStep2:(data) => dispatch(setLiveStockStep2(data)),
    handleSetStep:(value) => dispatch(setStep(value)),
});
  
export default withRouter(
connect(
    mapStateToProps,
    mapDispatchToProps
)(LiveStockStep2)
);