import * as types from "./actionTypes";

export const setLiveStockStep1 = data => ({
  type: types.SET_LIVE_STOCK_STEP_1,
  data
});
export const setLiveStockStep2 = data => ({
  type: types.SET_LIVE_STOCK_STEP_2,
  data
});
export const setStep = value => ({
  type: types.SET_STEPS,
  value
})
export const setFlagUpdate = value => ({
  type: types.SET_FLAG_UPDATE,
  value
})