import React, { Component } from 'react';
import Header from '../../components/Header/Header';
import StepIndicator from '../../components/ui/StepIndicator';
import LiveStockStep1 from '../../containers/livestockstep1';
import LiveStockStep2 from '../../containers/livestockstep2';
import LiveStockStep3 from '../../containers/livestockstep3';

class LiveStock extends Component {
    constructor() {
        super();
        this.state = {
          currentStep: 1,
          moveToNextFormStep1:true,
          moveToNextFormStep2:false,
          moveToNextFormStep3:false,
          open:false,
        };
    }
    
    componentDidUpdate (prevProps) {
        if(prevProps.currentStep !== this.props.currentStep ) {
            this.setState({currentStep:this.props.currentStep})
        }
    }
    handleOpenNavBar = () => {
        this.setState({open:!this.state.open})
    }
    render() {
        const stepsArray = ["Owner Information","Add Animal","Preview and Print"]
        return (
            <div>
                <div>
                    <div id="wrapper" className="wrapper">
                        <div id="st-container" className={this.state.open?"st-container st-effect-1 st-menu-open":"wrapper dashboard-wrapper st-container"}>
                            <Header 
                                header="Livestock Brand Inspection"
                                openNavBar={this.handleOpenNavBar}
                                open={this.state.open}/>
                            <div className="main-middle-area">
        
                                <section className="step-forms-section">
                                    <div className="step-forms-inner">
                                        <div className="container">
                                            {/* <!-- tabs top --> */}
                                            <div className="row">
                                                <div className="col-lg-12">
                                                    <StepIndicator 
                                                        currentStepNumber={this.state.currentStep - 1}
                                                        steps={stepsArray}/>
                                                </div>
                                            </div>
                                            {/* <!-- End of tabs top --> */}
                                            <div className="row">
                                                <div className="col-lg-12">
                                                    <div className="step-forms-tab-content">
                                                        <div className="tab-content">
                                                            {this.state.currentStep === 1 ?
                                                                <LiveStockStep1/>:null }
                                                            {this.state.currentStep === 2 ?
                                                                <LiveStockStep2/>:null }   
                                                            {this.state.currentStep === 3 ?
                                                                <LiveStockStep3
                                                                />:null }   
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                            {/* <!-- end --> */}
                                        </div>
                                    </div>
                                </section>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
export default LiveStock;