import React, { Component } from 'react'
import { Modal } from 'react-bootstrap';
import InputField from '../ui/InputField';
import { getUrl } from '../../services/network/urls';
import {  post } from '../../services/network/requests';
export default class DealerAddPopup extends Component {
    constructor(props) {
        super(props);
        this.state = {
            name : '',
            liecenceNo : '',
            nameErr: '',
            liecenceNoErr: '',
        };

    }

    componentDidUpdate(prevProps) {
       

    }

    onInputChangeHandler = (e) => {
        const { name, value } = e.target
        this.setState({ [name]: value }, () => {
            switch (name) {
                case 'name':
                    if (value === '') {
                        this.setState({ nameErr: "Dealer Name is required" })
                    } else {
                        this.setState({ nameErr: '' })
                    }
                    break;
                case 'liecenceNo':
                    if (value === '') {
                        this.setState({ liecenceNoErr: "Dealer Liecense No is required" })
                    } else {
                        this.setState({ liecenceNoErr: '' })
                    }
                    break;
                default:
                    break;
                }
        })
    }

    handelSubmitDealerName = () => {
        if (this.state.name !== '' && this.state.liecenceNo !== ''){
            const url = getUrl("add_brand_dealer");
            const data = JSON.stringify({
                name: this.state.name,
                lic_no: this.state.liecenceNo
            })
            post(url, data)
                .then((res) => {
                    const { data: { code,  status } } = res;
                        switch (code) {
                            case 200:
                                if (status) {
                                    this.handleClose();
                                }
                                break;
                            default:
                                break;
                        }
                })
                .catch((err) => console.log(err))
                
        }else{
            if(this.state.name === ''){
                this.setState({ nameErr: "Dealer Name is required" })
            }
            if (this.state.liecenceNo === '') {
                this.setState({ liecenceNoErr: "Dealer Liecense No is required" })
            }
        }
    }

    handleClose = () => {
        
        this.props.onHide();
    }

    render() {
        const { display } = this.props;

        return (
            <Modal className="modal modal-common fade" id="draw-mark-modal" show={display}
                onHide={this.handleClose}>
                <button onClick={this.handleClose} type="button" className="close" data-dismiss="modal">&times;</button>
                <div className="modal-dialog modal-dialog-centered modal-xl">
                    <div className="modal-content">
                        <div className="modal-header">
                            <h4 className="modal-title">Add Dealer</h4>
                        </div>
                        <div className="modal-body">

                            <div className="draw-slider-view-root">

                                <div className="form-div">
                                    <div className="row mr-minus-10 mt-5">
                                        <div className="col-lg-6 col-md-4 pr-10">
                                            <InputField
                                                placeholder="Enter Dealer Name"
                                                value={this.state.name}
                                                name="name"
                                                onChangeMethod={this.onInputChangeHandler}

                                            />
                                            {this.state.nameErr !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.nameErr}</div> : null}
                                        </div>
                                        <div className="col-lg-6 col-md-4 pr-10">
                                            <InputField
                                                placeholder="Enter Dealer Liecense No"
                                                value={this.state.liecenceNo}
                                                name="liecenceNo"
                                                onChangeMethod={this.onInputChangeHandler}

                                            />
                                            {this.state.liecenceNoErr !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.liecenceNoErr}</div> : null}
                                        </div>
                                    </div>
                                    
                                </div>

                            </div>

                        </div>
                        <div className="modal-footer pt-xs0">
                            <div className="col-footer">
                                <div className="btn-col width-xs-120 pr-10px">
                                    <button onClick={this.handleClose} type="button" className="btn btn-secondary" data-dismiss="modal">Cancel</button>
                                </div>
                                <div className="btn-col">
                                    <button onClick={this.handelSubmitDealerName} className="btn btn-primary btn-save">Save </button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </Modal>
        )

    }
}