import { connect } from 'react-redux';
import { withRouter } from 'react-router';
import LiveStockStep1 from '../components/LiveStock/LiveStockStep1';
import { setLiveStockStep1, setStep, setLiveStockStep2} from '../actions/liveStockAction';

const mapStateToProps = (state) => ({
  liveStockStep1Data : state.livestock.liveStockStep1,
});

const mapDispatchToProps = (dispatch) => ({
  handleLiveStockStep2: (data) => dispatch(setLiveStockStep2(data)),
  handleLiveStockStep1:(data) => dispatch(setLiveStockStep1(data)),
  handleSetStep:(value) =>dispatch(setStep(value)),
});
  
  
  export default  withRouter(
    connect(
      mapStateToProps,
      mapDispatchToProps
    )(LiveStockStep1)
  );