import { connect } from 'react-redux';
import { withRouter } from 'react-router';
import LiveStockStep3 from '../components/LiveStock/LiveStockStep3';
import {setStep, setFlagUpdate} from '../actions/liveStockAction';

const mapStateToProps = (state) => ({
  liveStockStep1Data : state.livestock.liveStockStep1,
  liveStockStep2Data :state.livestock.liveStockStep2,
  isFirstCall: state.livestock.isFirstCall

});
  
  const mapDispatchToProps = (dispatch) => ({
    handleSetStep:(value) => dispatch(setStep(value)),
    setFlagUpdate:(value) => dispatch(setFlagUpdate(value))
  });
  
  export default withRouter(
    connect(
      mapStateToProps,
      mapDispatchToProps
    )(LiveStockStep3)
  );