import React,{Component} from 'react';
import OwnerInfoTag from '../ui/OwnerInfoTag';
// import QrImg from '../../assets/images/qr-code.svg'
import moment from 'moment';
class OwnerInfo extends Component {
   
    render() {
        return (
            <div className="form-div">
                <div className="row mr-minus-10">
                    <div className="col-lg-12 col-md-12 pr-10">
                        <div className="ul-div">
                            <div className="info-div">
                                <div className="row mr-minus-10">
                                    <div className="col-lg-9 col-md-12 order-lg-1 order-md-2 order-2 pr-10">
                                        <h4>Owners information</h4>
                                        <div className="info-left-div">
                                            <OwnerInfoTag
                                                label = "Owner Name"
                                                value={this.props.ownerInfoData.owner_name}/>
                                            <OwnerInfoTag
                                                label = "Address"
                                                value={this.props.ownerInfoData.owner_street_address + ' ' + this.props.ownerInfoData.owner_city + ', ' + this.props.ownerInfoData.owner_state + ' ' + this.props.ownerInfoData.owner_zip_code + ' ' + this.props.ownerInfoData.owner_county  }/>
                                            <OwnerInfoTag
                                                label = "Buyer Name"
                                                value={this.props.ownerInfoData.buyer_name}/>
                                            <OwnerInfoTag
                                                label = "Address"
                                                value={this.props.ownerInfoData.buyer_street_address + ' ' + this.props.ownerInfoData.buyer_city + ', ' + this.props.ownerInfoData.buyer_state + ' ' + this.props.ownerInfoData.buyer_zip_code + ' ' + this.props.ownerInfoData.buyer_county  }/>
                                            <OwnerInfoTag
                                                label = "Sold to"
                                                value = {this.props.ownerInfoData.sold_to}/>
                                             <OwnerInfoTag
                                                label = "Destination"
                                                value = {this.props.ownerInfoData.destination}/>
                                             <OwnerInfoTag
                                                label = "Consigned to"
                                                value = {this.props.ownerInfoData.consigned_to}/>
                                             <OwnerInfoTag
                                                label = "Date"
                                                value = {moment(this.props.ownerInfoData.date !== undefined? this.props.ownerInfoData.date: new Date()).format("DD MM YYYY")}/>
                                            <OwnerInfoTag
                                                label = "Carrier"
                                                value = {this.props.ownerInfoData.carrier}/>
                                            <div className="col-div m-0"></div>
                                            <OwnerInfoTag
                                                label = "LIC. NO."
                                                value = {this.props.ownerInfoData.lic_no}/>
                                        </div>
                                    </div>
                                    <div className="col-lg-3 col-md-12 order-lg-2 order-md-1 order-1 pr-10">
                                        <div className="thumb-div d-flex justify-content-end">
                                            <img src={this.props.qr_code} className="img-fluid scan-qr-img" alt="qr-code" />
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                          
            </div> 
        )
    }
}
export default OwnerInfo;