import React, { Component } from 'react';
import OwnerInfo from './OwnerInfo';
import { getUrl } from '../../services/network/urls';
import {  put } from '../../services/network/requests';
import { Link } from 'react-router-dom'

class LiveStockStep3 extends Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedOption: (this.props.liveStockStep2Data.payment_by !== null && this.props.liveStockStep2Data.payment_by !== undefined) ? this.props.liveStockStep2Data.payment_by: 'PaidByCash',
            isFirstTime: true,
        }
    }

    componentDidMount() {
        this.setState({ isFirstTime: this.props.isFirstCall })
    }

    getTotal() {
        let grandTotal = 0;
        const rowTotals = this.props.liveStockStep2Data.addAnimalRow.map(
            row => (row.quantity) || 0
        );
        if (rowTotals.length > 0) {
            grandTotal = rowTotals.reduce((acc, val) => parseInt(acc) + parseInt(val));
        }
        return grandTotal;
    }

    handlePrevious = () => {
        const currentStep = 2;
        this.props.handleSetStep(currentStep);
    }

    handleOptionChange = (changeEvent) => {
        this.setState({
            selectedOption: changeEvent.target.value
        });
        
    };

    handleSubmit = () => {
        const stepTwoData = this.props.liveStockStep2Data;
        
        const step3Data = JSON.stringify({
            payment_by: this.state.selectedOption
        })
        const url = getUrl("update_live_stock_step_3");
        put(`${url}/${stepTwoData.id}/step-3/`,step3Data)
        .then(res=>{
            const { data: { code,   status } } = res;
            switch (code) {
                case 200:
                    if (status) {
                        alert('data saved Successfull');
                    }
                    break;
                default:
                    break;
            }
        })
        .catch((err)=>{
            console.log(err)
        })
    }

    render() {
        return (
            <div id="menu3" className="tab-pane tab-pane-three fade active show  ">
                <div className="card panel panel-default panel-custom panel-custom2">
                    <div className="card-header panel-heading">
                        <h2>Preview and print</h2>
                    </div>
                    <div className="card-body panel-body panel-body1 pb-10">
                        <OwnerInfo
                            ownerInfoData={this.props.liveStockStep1Data}
                            qr_code = {this.props.liveStockStep2Data.qr_code} />
                    </div>
                    <div className="card-body panel-body panel-body1 pb-10">
                        <div className="form-div">
                            <div className="row mr-minus-10">
                                <div className="col-lg-12 col-md-12 pr-10">
                                    <div className="ul-div">
                                        <h4>Inspected for</h4>
                                        <div className="info-div">
                                            <div className="row mr-minus-10">
                                                <div className="col-lg-9 col-md-12 pr-10">
                                                    <div className="info-left-div">
                                                        <div className="col-div tab-col-40">
                                                            <ul className="custom-ul">
                                                                {this.props.liveStockStep2Data.inspectedFor.map((value, index) => {
                                                                    return (<li key={index}>{value}</li>)
                                                                })}
                                                            </ul>
                                                        </div>
                                                        <div className="col-div tab-col-60 flex-column">
                                                            <div className="full-row d-flex mb-20">
                                                                <div className="col-div flex-column">
                                                                    <div className="label-div full-width-div">
                                                                        <h5 className="mb-5px">Owner Permises No.</h5>
                                                                        <p>{this.props.liveStockStep2Data.ownerPermissionNo}</p>
                                                                    </div>
                                                                </div>
                                                                <div className="col-div flex-column">
                                                                    <div className="label-div full-width-div">
                                                                        <h5 className="mb-5px">GPS Coordinates</h5>
                                                                        <p>{this.props.liveStockStep2Data.gps_co_ordinates}</p>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                            <div className="full-row">
                                                                <div className="full-div">
                                                                    <div className="label-div full-width-div">
                                                                        <h5 className="mb-5px">Dealers License No. Required by UTAH State Law</h5>
                                                                        <p>{this.props.liveStockStep2Data.dealers_license_no}</p>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div className="col-lg-3 col-md-12 pr-10">
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="card-body panel-body panel-body1 pb-10">
                        <div className="form-div">
                            <div className="row mr-minus-10">
                                <div className="col-lg-12 col-md-12 pr-10">
                                    <div className="ul-div">
                                        <h4>Added Animals</h4>
                                        <div className="table-root-div">
                                            <div className="row mr-minus-10">
                                                <div className="col-lg-12 col-md-12 pr-10">
                                                    <div className="table-responsive">
                                                        <table className="table table-bordered table-custom mb-20">
                                                            <thead>
                                                                <tr>
                                                                    <th>Animal</th>
                                                                    <th>Brand</th>
                                                                    {/* <th>Location</th> */}
                                                                    <th>color</th>
                                                                    <th>Breed</th>
                                                                    <th>Total</th>
                                                                </tr>
                                                            </thead>
                                                            {this.props.liveStockStep2Data.addAnimalRow.length > 0 ?
                                                                <tbody>
                                                                    {this.props.liveStockStep2Data.addAnimalRow.map(((row, index) => {
                                                                        return (
                                                                            <tr key={index}>
                                                                                <td className="font-bold">{row.animal.label}</td>
                                                                                <td>{row.brand.label}</td>
                                                                                {/* <td>{row.location.label}</td> */}
                                                                                <td>{row.color.label}</td>
                                                                                <td>{row.breed.label}</td>
                                                                                <td>{row.quantity}</td>
                                                                            </tr>)
                                                                    }))}
                                                                    <tr>
                                                                        <td className="text-right plr-30" colspan="4">Total Animal</td>
                                                                        <td className="font-bold">{this.getTotal()}</td>
                                                                    </tr>
                                                                </tbody> : <tbody>
                                                                    <td>No Data Found</td>
                                                                </tbody>
                                                            }
                                                        </table>
                                                    </div>
                                                </div>
                                            </div>
                                            <div className="row mr-minus-10">
                                                <div className="col-lg-6 col-md-6 pr-10 order-lg-1 order-md-1 order-2">
                                                    <div className="content-small-div width-50 sm-width-100 mx-width-370 mb-20">
                                                        <p className="title-p font-underline">Validation of brand certificates</p>
                                                        <p>Brand inspection certificates, except for Change of ownershipand Travel permits shall be automatically cancelled and void one week after time of issuance. This is certificate void if altered.</p>
                                                    </div>
                                                    <div className="radio-div">
                                                        <div className="form-group">
                                                            <h3>Payment by</h3>
                                                            <div className="radio-button-div">
                                                                <div className="custom-control custom-radio ">
                                                                    <input type="radio" className="custom-control-input" id="paid-cash" name="paid" value="PaidByCash" checked={this.state.selectedOption === 'PaidByCash'}
                                                                        onChange={this.handleOptionChange}
                                                                    />
                                                                    <label className="custom-control-label" htmlFor="paid-cash">Paid by Cash</label>
                                                                </div>
                                                                <div className="custom-control custom-radio">
                                                                    <input type="radio" className="custom-control-input" id="paid-cheque" name="paid" value="PaidByCheque" checked={this.state.selectedOption === 'PaidByCheque'}
                                                                        onChange={this.handleOptionChange}
                                                                    />
                                                                    <label className="custom-control-label" htmlFor="paid-cheque">Paid by Cheque</label>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div className="col-lg-6 col-md-6 pr-10 order-lg-2 order-md-2 order-1 ">
                                                    <div className="sub-table-div">
                                                        <div className="tr-row">
                                                            <div className="td-col"><p>Cattle Inspection</p></div>
                                                            <div className="td-col td-col-prise"><p>$40</p></div>
                                                        </div>
                                                        <div className="tr-row">
                                                            <div className="td-col"><p>Beef Council</p></div>
                                                            <div className="td-col td-col-prise"><p>$30</p></div>
                                                        </div>
                                                        <div className="tr-row">
                                                            <div className="td-col"><p>Predator Control</p></div>
                                                            <div className="td-col td-col-prise"><p>$20</p></div>
                                                        </div>
                                                        <div className="tr-row">
                                                            <div className="td-col"><p>Horse Inspection</p></div>
                                                            <div className="td-col td-col-prise"><p>-</p></div>
                                                        </div>
                                                        <div className="tr-row tr-row-final">
                                                            <div className="td-col"><p>Inspection Charge Total</p></div>
                                                            <div className="td-col td-col-prise"><p>$90</p></div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="tab-footer">
                    <div className="row ">
                        <div className="col-lg-12 justify-content-end pt-10">
                            <div className="right-side-footer right-side-footer-final d-flex d-flex-sm align-items-end justify-content-between">

                                <div className="width-100 width-right d-flex sm-width-100 print-group-sm">

                                    <div className="step-btn width-50 sm-width-auto order-md-1 order-2 pritn-group-btn">
                                        
                                        <Link target="_blank" to={`/livestock/${this.props.liveStockStep2Data.owner_information}/print/owners`} className="btn btn-secondary2 btn-previous max-width-25"> <i className="fe fe-printer"></i> <span className="remove-mobile"> &nbsp; Print Owner Copy</span></Link>
                                        <Link target="_blank" to={`/livestock/${this.props.liveStockStep2Data.owner_information}/print/sellers`} className="btn btn-secondary2 btn-previous max-width-25"> <i className="fe fe-printer"></i> <span className="remove-mobile"> &nbsp; Print Seller Copy</span></Link>
                                        <Link target="_blank" to={`/livestock/${this.props.liveStockStep2Data.owner_information}/print/transportation`} className="btn btn-secondary2 btn-previous max-width-25"> <i className="fe fe-printer"></i> <span className="remove-mobile"> &nbsp; Print Transportation Copy</span></Link>
                                    </div>
                                    

                                    <div className="step-btn width-25 sm-width-50 pr-10 order-md-2 order-1">
                                        <button href="#" className="btn btn-secondary btn-previous" onClick={this.handlePrevious}>Previous <span className="remove-mobile"> &nbsp; step</span></button>
                                    </div>
                                    <div className="step-btn width-25 sm-width-50 order-md-3 order-3">
                                        <button href="#" onClick={this.handleSubmit} className="btn btn-primary btn-save">save</button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}
export default LiveStockStep3;