import React, { Component } from 'react';
import InputField from '../ui/InputField';
import Checkbox from '../ui/Checkbox';
import AddAnimal from './AddAnimal';
import { getUrl } from '../../services/network/urls';
import {get,post,put} from '../../services/network/requests';
import DealerAddPopup from '../LiveStock/DealerAddPopup';
class LiveStockStep2 extends Component {
    state = {
        quantity: '',
        specify: this.props.liveStockStep2Data.specify !== '' ? this.props.liveStockStep2Data.specify : '',
        ownerPermissionNo: this.props.liveStockStep2Data.Owner_permises_no !== '' ? this.props.liveStockStep2Data.Owner_permises_no : '',
        gps_co_ordinates: this.props.liveStockStep2Data.gps_co_ordinates !== '' ? this.props.liveStockStep2Data.gps_co_ordinates : '',
        dealerId:this.props.liveStockStep2Data.dealerId !== "" ? this.props.liveStockStep2Data.dealerId : '',
        dealers_license_no: this.props.liveStockStep2Data.dealers_license_no !== '' ? this.props.liveStockStep2Data.dealers_license_no : '',
        id:this.props.liveStockStep2Data.id !== '' ?this.props.liveStockStep2Data.id:'' ,
        latitude:this.props.liveStockStep2Data.latitude !== '' ?this.props.liveStockStep2Data.latitude:'',
        longitude:this.props.liveStockStep2Data.longitude !== '' ?this.props.liveStockStep2Data.longitude:'',
        owner_information :this.props.liveStockStep2Data.owner_information !== '' ?this.props.liveStockStep2Data.owner_information:'',
        qr_code:this.props.liveStockStep2Data.qr_code !== '' ?this.props.liveStockStep2Data.qr_code:'',
        total_animal:this.props.liveStockStep2Data.total_animal !== '' ?this.props.liveStockStep2Data.total_animal:'',
        addAnimalRow: [
            {
                id: 0,
                isClosed: false,
                animal: '',
                brand: '',
                location: '',
                color: '',
                breed: '',
                quantity: '',
                errAnimal: '',
                errBrand: '',
                errLocation: '',
                errcolor: '',
                errBreed: '',
                errQuantity: '',
                showColor:true,
                showBreed:true,
                showGender:true,
            }, {
                id: 1,
                isClosed: false,
                animal: '',
                brand: '',
                location: '',
                color: '',
                breed: '',
                quantity: '',
                errAnimal: '',
                errBrand: '',
                errLocation: '',
                errcolor: '',
                errBreed: '',
                errQuantity: '',
                showColor:true,
                showBreed:true,
                showGender:true,
            }
        ],
        inspectedFor: (this.props.liveStockStep2Data.inspectedFor !== undefined && this.props.liveStockStep2Data.inspectedFor.length !== 0) ? this.props.liveStockStep2Data.inspectedFor : [],
        checkBoxData1: [
            { id: "change_to_ownership", name: "Inspected", label: "Change to ownership", isChecked: false },
            { id: "released_at_no_charge", name: "Inspected", label: "Released at no charge", isChecked: false },
            { id: "life_time_travel_permit", name: "Inspected", label: "Life time travel permit", isChecked: false },
            { id: "leaving_the_state", name: "Inspected", label: "Leaving the state", isChecked: false },
            { id: "yearly_travel_permit", name: "Inspected", label: "Yearly travel permit", isChecked: false },
            { id: "slaughter", name: "Inspected", label: "Slaughter", isChecked: false },
            { id: "other", name: "Inspected", label: "Other", isChecked: false }
        ],
        ownerPermissionNoErr: '',
        gps_co_ordinatesErr: '',
        dealers_license_noErr: '',
        specifyErr: '',
        inspectedForErr: '',
        error: "",
        isSpecifyTrue: false,
        dropDownOptions:{
            animal: [],
            brand: [],
            location: [],
            color: [],
            breed: [],
        },
        animalData:'',
        dealerData:[],
        dealerAddPopup : false,
    };

    handlePopUpForAddDealer = ()  => {
        this.setState({
            dealerAddPopup: !this.state.dealerAddPopup
        })
    }
    getLocation = () => {
        if (navigator.geolocation) {
            return navigator.geolocation.getCurrentPosition(this.showPosition);
        } else {
            console.log("Geo Location not supported by browser");
        }
    }
    showPosition = (position) => {
        var longitude = position.coords.longitude.toFixed(7);
        var latitude = position.coords.latitude.toFixed(7);
        var convertLat = Math.abs(latitude);
        var latDeg = Math.floor(convertLat);
        var latMin = (Math.floor((convertLat - latDeg) * 60).toFixed(2));
        var latSeconds = ((convertLat - latDeg - latMin / 60) * Math.pow(60, 2)).toFixed(2);
        var latCardinal = ((latitude > 0) ? "N" : "S");

        var convertLng = Math.abs(longitude);
        var lngDeg = Math.floor(convertLng);
        var lngMin = (Math.floor((convertLng - lngDeg) * 60).toFixed(2));
        var lngSeconds = ((convertLng - lngDeg - lngMin / 60) * Math.pow(60, 2)).toFixed(2);
        var lngCardinal = ((longitude > 0) ? "E" : "W");
        this.setState({
            gps_co_ordinates: latDeg + '' + latMin + "'" + latSeconds + '"' + latCardinal + ' ' + lngDeg + '' + lngMin + "'" + lngSeconds + '"' + lngCardinal
        })

    }
    onInputChangeHandler = (e) => {
        const { name, value } = e.target
        this.setState({ [name]: value }, () => {
            switch (name) {
                case 'ownerPermissionNo':
                    if (value === '') {
                        this.setState({ ownerPermissionNoErr: "Owner Permission No is required" })
                    } else {
                        this.setState({ ownerPermissionNoErr: '' })
                    }
                    break;
                case 'gps_co_ordinates':
                    if (value === '') {
                        this.setState({ gps_co_ordinatesErr: "Gps Co-ordinate is required" })
                    } else {
                        this.setState({ gps_co_ordinatesErr: '' })
                    }
                    break;
                case 'dealers_license_no':
                    if (value === '') {
                        this.setState({ dealers_license_noErr: "Dealers License No is required", activeSuggestionForDealer: false })
                    } else {
                        const url = getUrl("get_brand_dealer");
                        get(`${url}?query=${this.state.dealers_license_no}`)
                        .then(res => {
                            const { data: { code, data, status } } = res;
                            switch (code) {
                                case 200:
                                    if (status) {
                                        this.setState({ dealerData: data.result ,dealers_license_noErr: '', activeSuggestionForDealer: true, })
                                    }
                                    break;
                                default:
                                    break;
                            }
                        })
                        .catch(error => console.log(error))
                    }
                    break;
                case 'specify':
                    if (value === '') {
                        this.setState({ specifyErr: "Specify is required" })
                    } else {
                        this.setState({ specifyErr: '' })
                    }
                    break;
                default:
                    break;
            }
        })
    }

    onCheckHandler = (e, data) => {
        //Add Checked Value in array
        if (e.target.checked) {
            this.state.inspectedFor.push(data.id);
            this.setState({ inspectedForErr: '' })
        } else {
            let index = this.state.inspectedFor.indexOf(data.id)
            this.state.inspectedFor.splice(index, 1)
        }
        //changes checked event
        const updateData = [...this.state.checkBoxData1];
        updateData.map((update) => {
            if (update.id === data.id) {
                update.isChecked = e.target.checked;
            }
            if (data.label === 'Other') {
                if (data.isChecked === true) {
                    this.setState({ isSpecifyTrue: true })
                } else {
                    this.setState({ isSpecifyTrue: false })
                }
            }
            return true
        });
        this.setState({
            checkBoxData1: updateData,
            inspectedFor: this.state.inspectedFor
        })
    }
    OnAddAnimalHandler = () => {
        const addRecord = {
            id: Math.random(),
            isClosed: true,
            animal: '',
            brand: '',
            location: '',
            color: '',
            breed: '',
            quantity: '',
            errAnimal: '',
            errBrand: '',
            errLocation: '',
            errcolor: '',
            errBreed: '',
            errQuantity: '',
            showColor: true,
            showBreed: true,
            showGender: true,
        };
        let newAnimalRecord = [...this.state.addAnimalRow, addRecord];
        this.setState({ addAnimalRow: newAnimalRecord });
    }
    
    isFormValid = () => {
        const { ownerPermissionNo, gps_co_ordinates, dealers_license_no, inspectedFor } = this.state;
        let addAnimalRow = [...this.state.addAnimalRow];
        let isValid = true;
        addAnimalRow.map((data, index) => {
            if (data.id === 0) {
                if (data.animal === '') {
                    isValid = false;
                    data.errAnimal = 'Select Animal';
                }
                if (data.brand === '') {
                    isValid = false;
                    data.errBrand = 'Select Brand';
                }
                // if (data.location === '') {
                //     isValid = false;
                //     data.errLocation = 'Select Location';
                // }
                if (data.color === '') {
                    isValid = false;
                    data.errcolor = 'Select color';
                }
                if (data.breed === '') {
                    isValid = false;
                    data.errBreed = 'Select Breed';
                }
                if (data.quantity === '') {
                    isValid = false;
                    data.errQuantity = 'Add Quantity';
                }
            } else if (data.animal !== '' || data.brand !== '' || data.location !== '' || data.color !== '' || data.breed !== '' || data.quantity !== '') {
                if (data.animal === '') {
                    isValid = false;
                    data.errAnimal = 'Select Animal';
                }
                if (data.brand === '') {
                    isValid = false;
                    data.errBrand = 'Select Brand';
                }
                // if (data.location === '') {
                //     isValid = false;
                //     data.errLocation = 'Select Location';
                // }
                if (data.color === '') {
                    isValid = false;
                    data.errcolor = 'Select color';
                }
                if (data.breed === '') {
                    isValid = false;
                    data.errBreed = 'Select Breed';
                }
                if (data.quantity === '') {
                    isValid = false;
                    data.errQuantity = 'Add Quantity';
                }
            }
            return data;
        });
        this.setState({ addAnimalRow });
        if (ownerPermissionNo === '' || ownerPermissionNo === undefined ) {
            isValid = false;
            this.setState({ ownerPermissionNoErr: "Owner Permission No is required" })
        }
        if (gps_co_ordinates === '') {
            isValid = false;
            this.setState({ gps_co_ordinatesErr: "Gps Co-ordinate is required" })
        }
        if (dealers_license_no === undefined || dealers_license_no === '') {
            isValid = false;
            this.setState({ dealers_license_noErr: "Dealers License No is required" })
        }
        if (inspectedFor.length === 0) {
            isValid = false;
            this.setState({ inspectedForErr: "Check any one is required" })
        }
        if (this.state.isSpecifyTrue === true) {
            if (this.state.specify === '' || this.state.specify === undefined) {
                isValid = false;
                this.setState({ specifyErr: 'Specify is required' })
            }
        }
        return isValid;
    }

    handleCheckForm = () => {
        this.setState({ error: '' });
        const isValid = this.isFormValid();
        if (isValid) this.submitForm()
    }

    submitForm = () => {
        var rowsData = this.state.addAnimalRow.filter(row => { return row.animal !== ""});
        
        const newRowData = rowsData.map(({animal,brand,location,color,breed,quantity})=>(
            {
                category:animal.value,
                brand:brand.value,
                colour:color.value,
                breed:breed.value,
                quantity:quantity,
                owner_information:this.props.liveStockStep1Data.id
            }
        ));
        var result = {};
        for (var i = 0; i <this.state.checkBoxData1.length; i++) {
            result[this.state.checkBoxData1[i].id] = this.state.checkBoxData1[i].isChecked;
        }
        var url = window.location.href
            var arr = url.split("/");
            var locationUrl = arr[0] + "//" + arr[2]
        const newStep2Data = JSON.stringify({
            change_to_ownership: result.change_to_ownership,
            released_at_no_charge: result.released_at_no_charge,
            life_time_travel_permit: result.life_time_travel_permit,
            leaving_the_state:result.leaving_the_state,
            yearly_travel_permit: result.yearly_travel_permit,
            slaughter: result.slaughter,
            other: result.other,
            Owner_permises_no: this.state.ownerPermissionNo,
            gps_coordinates:this.state.gps_co_ordinates,
            dealer_license_no:this.state.dealerId,
            owner_information: this.props.liveStockStep1Data.id,
            latitude: 23.5654,
            longitude: 72.454,
            total_animal: newRowData.length,
            aminal_row: newRowData,
            url: locationUrl + '/livestock/edit/' 
        })
        if (this.props.liveStockStep2Data.id === null) {
            const url = getUrl("save_live_stock_step_2")
            post(url,newStep2Data)
            .then((res)=>{
                const { data: { code, data,  status } } = res;
                switch (code) {
                    case 200:
                        if (status) {
                            const step2Data = {
                                Owner_permises_no: data.Owner_permises_no,
                                addAnimalRow: rowsData,
                                dealerId :this.state.dealerId,
                                dealers_license_no: this.state.dealers_license_no,
                                id:data.id,
                                latitude:data.latitude,
                                longitude:data.longitude,
                                owner_information :data.owner_information,
                                qr_code:data.qr_code,
                                specify:data.specify,
                                total_animal:data.total_animal,
                                inspectedFor: this.state.inspectedFor,
                                gps_co_ordinates: this.state.gps_co_ordinates,  
                                payment_by: this.props.liveStockStep2Data.payment_by      
                            }
                            this.props.handleLiveStockStep2(step2Data)
                            const currentStep = 3
                            this.props.handleSetStep(currentStep)
                        }
                        break;
                    default:
                        break;
                }
            })
            .catch((err)=>{
                console.log(err)
            })
        } else {
            const updateUrl = getUrl("update_live_stock_step_2")
            put(`${updateUrl}/${this.props.liveStockStep2Data.id}/step-2/`,newStep2Data)
            .then((res)=>{
                const { data: { code, data,  status } } = res;
                switch (code) {
                    case 200:
                        if (status) {
                            const step2Data = {
                                Owner_permises_no: data.Owner_permises_no,
                                addAnimalRow: rowsData,
                                dealerId :data.dealer_license_no,
                                dealers_license_no: this.state.dealers_license_no,
                                id:data.id,
                                latitude:data.latitude,
                                longitude:data.longitude,
                                owner_information :data.owner_information,
                                qr_code:data.qr_code,
                                specify:data.specify,
                                total_animal:data.total_animal,
                                inspectedFor: this.state.inspectedFor,
                                gps_co_ordinates: this.state.gps_co_ordinates,  
                                payment_by: this.props.liveStockStep2Data.payment_by        
                            }
                            this.props.handleLiveStockStep2(step2Data)
                            const currentStep = 3
                            this.props.handleSetStep(currentStep)
                        }
                        break;
                    default:
                        break;
                }
            })
            .catch((err)=>{
                console.log(err)
            })
        }
    }

    onRemoveAnimalHandler = (index, e) => {
        this.state.addAnimalRow.splice(index, 1);
        this.setState({
            addAnimalRow: this.state.addAnimalRow
        });
    }

    handleAutoComplete = (name, license_no,data) => {
        this.setState({ 
            dealers_license_no: data.lic_no, 
            dealers_name: data.name,
            dealerId:data.id, 
            activeSuggestionForDealer: false, dealers_license_noErr: '' })
    }

    handleOnChangeDropdown = (selected, index) => {
        const { addAnimalRow } = this.state;
        addAnimalRow.map((data, key) => {
            console.log(key,data);
            
            if (data.id === index) {
                switch (selected.type) {
                    case 'animal':
                        data.animal = selected; 
                        // const SlectedData = {value:"1",label:"Horse",type:"animal"} ///this is temporary
                        // data.animal = SlectedData;
                        const nameOfAnimal = selected.label
                        
                        const updateDropDownOption = this.state.dropDownOptions;
                        updateDropDownOption.brand = this.state.animalData[nameOfAnimal].brand.map(({id:value,name:label})=>({value,label,type:"brand"}));
                        updateDropDownOption.color = this.state.animalData[nameOfAnimal].color.map(({id:value,name:label})=>({value,label,type:"color"}));
                        updateDropDownOption.breed = this.state.animalData[nameOfAnimal].breed.map(({id:value,name:label})=>({value,label,type:"breed"}));
                        this.setState({dropDownOptions:updateDropDownOption})
                        data.errAnimal = '';
                        if (nameOfAnimal === 'Cattle' || nameOfAnimal === 'Sheep'){
                            data.showColor = false;
                            data.showBreed = true;
                            data.showGender = true;
                        }
                        if (nameOfAnimal === 'Elk'){
                            data.showColor = false;
                            data.showBreed = false;
                            data.showGender = false;
                        }
                        
                        break;
                    case 'brand':
                        data.brand = selected;
                        data.errBrand = '';
                        break;
                    // case 'location':
                    //     data.location = selected;
                    //     data.errLocation = '';
                    //     break;
                    case 'color':
                        data.color = selected;
                        data.errcolor = '';
                        break;
                    case 'breed':
                        data.breed = selected;
                        data.errBreed = '';
                        break;
                    default:
                        break;
                }
            }
            return data;
        });
        this.setState({ addAnimalRow });
    }

    handleQuantity = (e, value) => {
        let addAnimalRow = [...this.state.addAnimalRow];
        addAnimalRow.map((data) => {
            if (data.id === value.id) {
                data.quantity = e.target.value;
                if (data.quantity !== '') {
                    data.errQuantity = '';
                }
            }
            return data;
        });
        this.setState({ addAnimalRow });
    }

    async componentDidMount() {
        await this.getLocation()
        await this.getAnimalInitData();
        await this.state.checkBoxData1.map((data) => {
            if (this.props.liveStockStep2Data.inspectedFor.length !== 0) {
                this.props.liveStockStep2Data.inspectedFor.map(inspect => {
                    if (inspect === data.id) {
                        data.isChecked = true;
                    }
                    return true;
                })
            }
            return true;
        });
        console.log(this.state.dropDownOptions);
        
        
        // const { checkBoxData1, checkBoxData2, checkBoxData3 } = this.state;
        // this.setState({ checkBoxData1, checkBoxData2, checkBoxData3 })
    }

    getAnimalInitData = () => {
        const url = getUrl("get_animal_init");
        get(url)
            .then(res => {
                const animal = res.data.data;
                const animalOption = Object.values(animal).map(({id:value,name:label})=>({value,label,type:"animal"}))
                const updateDropDownOption = this.state.dropDownOptions;
                updateDropDownOption.animal = animalOption;
                updateDropDownOption.brand = animal[animalOption[0].label].brand.map(({id:value,name:label})=>({value,label,type:"brand"}));
                updateDropDownOption.color = animal[animalOption[0].label].color.map(({id:value,name:label})=>({value,label,type:"color"}));
                updateDropDownOption.breed = animal[animalOption[0].label].breed.map(({id:value,name:label})=>({value,label,type:"breed"}));
                this.setState({animalData:animal,dropDownOptions:updateDropDownOption},() => {
                    if (this.props.liveStockStep2Data.addAnimalRow.length !== 0) {
                        // let updateRow = this.state.addAnimalRow.map(data => this.props.liveStockStep2Data.addAnimalRow.find(row => row.id === data.id) || data);
                        if (this.props.match.params.id !== undefined && this.props.liveStockStep2Data.addAnimalRow[0].category !== undefined) {
                            let addAnimalRow = [];
                            this.props.liveStockStep2Data.addAnimalRow.map((value, index) => {
                                let animal = this.state.dropDownOptions.animal.filter((e) => e.label === value.category.animal_name)
                                let brand = this.state.dropDownOptions.brand.filter((e) => e.label === value.brand.brand)
                                let color = this.state.dropDownOptions.color.filter((e) => e.label === value.colour.color)
                                let breed = this.state.dropDownOptions.breed.filter((e) => e.label === value.breed.breed)
                                let animaldata = {
                                    id: value.id,
                                    isClosed: true,
                                    animal: animal[0],
                                    brand: brand[0],
                                    location: value.location,
                                    color: color[0],
                                    breed: breed[0],
                                    quantity: value.quantity,
                                    errAnimal: '',
                                    errBrand: '',
                                    errLocation: '',
                                    errcolor: '',
                                    errBreed: '',
                                    errQuantity: '',
                                    
                                }
                                addAnimalRow.push(animaldata);
                            })
                            this.setState({ addAnimalRow: addAnimalRow })
                        } else {
                            this.setState({ addAnimalRow: this.props.liveStockStep2Data.addAnimalRow })
                        }
                    } else {
                        this.setState({ addAnimalRow: this.state.addAnimalRow })
                    }
                })
            })
            .catch(error => console.log(error))
    }

    handlePrevious = () => {
        var rowsData = this.state.addAnimalRow.filter(row => { return row.animal !== ""; });
        const step2Data = JSON.stringify({
            ownerPermissionNo: this.state.ownerPermissionNo,
            addAnimalRow: rowsData,
            dealerId :this.state.dealerId,
            dealers_license_no: this.state.dealers_license_no,
            id:this.state.id,
            latitude:this.state.latitude,
            longitude:this.state.longitude,
            owner_information :this.state.owner_information,
            qr_code:this.state.qr_code,
            specify:this.state.specify,
            total_animal:this.state.total_animal,
            inspectedFor: this.state.inspectedFor,
            gps_co_ordinates: this.state.gps_co_ordinates,   
        });
        this.props.handleLiveStockStep2(JSON.parse(step2Data))
        const currentStep = 1;
        this.props.handleSetStep(currentStep);
    }

    render() {
        return (
            <div id="menu2" className="tab-pane tab-pane tab-pane-two fade active show ">
                {this.state.addAnimalRow.map((animalRecord, index) => {
                    return <AddAnimal
                        key={index}
                        animalRecord={animalRecord}
                        dropDownOptions={this.state.dropDownOptions}
                        index={index}
                        handleChange={(selected) => this.handleOnChangeDropdown(selected, animalRecord.id)}
                        removeAnimalRecord={this.onRemoveAnimalHandler}
                        onInputChangeHandler={this.handleQuantity}
                    />
                })}
                <div className="btn-add-row">
                    <button className="btn btn-block btn-add" onClick={this.OnAddAnimalHandler}>+ Add Animal</button>
                </div>
                <div className="card panel panel-default panel-custom">
                    <div className="card-header panel-heading">
                        <h2>Inspected for</h2>
                    </div>
                    <div className="card-body panel-body pb-10">
                        <div className="form-div">
                            <div className="row mr-minus-10">
                                {this.state.checkBoxData1.map((data, index) => {
                                    return (
                                        <div key={index + 'checkbox'} className="col-lg-3 col-md-4 pr-10">
                                            <Checkbox
                                                onCheckChange={(e) => this.onCheckHandler(e, data)}
                                                checked={data.isChecked}
                                                key={index}
                                                id={data.id}
                                                name={data.name}
                                                label={data.label} />
                                        </div>
                                    )
                                })}
                            </div>
                            {this.state.inspectedForErr !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-10px 0px 15px 0px' }}>{this.state.inspectedForErr}</div> : null}
                            <div className="row mr-minus-10 mt-5">
                                <div className="col-lg-3 col-md-4 pr-10">
                                    <InputField
                                        placeholder="specify"
                                        value={this.state.specify}
                                        name="specify"
                                        onChangeMethod={this.onInputChangeHandler}
                                        disabled={!this.state.isSpecifyTrue}
                                    />
                                    {this.state.specifyErr !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.specifyErr}</div> : null}
                                </div>
                            </div>
                            <div className="row mr-minus-10">
                                <div className="col-lg-3 col-md-4 pr-10">
                                    <InputField
                                        placeholder="Owner Permises No."
                                        value={this.state.ownerPermissionNo}
                                        name="ownerPermissionNo"
                                        onChangeMethod={this.onInputChangeHandler}
                                    />
                                    {this.state.ownerPermissionNoErr !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.ownerPermissionNoErr}</div> : null}
                                </div>
                                <div className="col-lg-3 col-md-4 pr-10">
                                    <InputField
                                        placeholder="GPS Coordinate"
                                        value={this.state.gps_co_ordinates}
                                        name="gps_co_ordinates"
                                        disabled="true"
                                        onChangeMethod={this.onInputChangeHandler}
                                    />
                                    {this.state.gps_co_ordinatesErr !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.gps_co_ordinatesErr}</div> : null}
                                </div>
                                <div className="col-lg-6 col-md-12 pr-10">
                                    <InputField 
                                        placeholder="Dealers License No. Required by UTAH State Law"
                                        value={this.state.dealers_license_no}
                                        name="dealers_license_no"
                                        onChangeMethod={this.onInputChangeHandler}
                                        // handleBlur={this.onInputBlurChangeHandler}
                                    />
                                    {this.state.activeSuggestionForDealer === true && this.state.dealers_license_no.length >= 1 &&
                                        <ul className="typeahead dropdown-menu" style={{ top: "46px", left: "10px", width: "221.328px", display: "block" }}>
                                            {this.state.dealerData.length > 0 ?
                                                this.state.dealerData.map((data, index) => {
                                                    return <li key={index} name="dealers_license_no" onClick={() => this.handleAutoComplete(data.name, data.lic_no,data)}>
                                                        <label>{data.name}</label>
                                                        <label className="address-link">{data.lic_no}</label>
                                                    </li>
                                                }) :
                                                <li name="dealers_license_no" onClick={this.handlePopUpForAddDealer} className="active" >
                                                    <label >Add Dealer !</label>
                                                </li>
                                            }
                                        </ul>
                                    }
                                    {this.state.dealers_license_noErr !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.dealers_license_noErr}</div> : null}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="tab-footer">
                    <div className="row ">
                        <div className="col-lg-12 justify-content-end pt-10">
                            <div className="right-side-footer d-flex d-flex-sm align-items-end justify-content-end">
                                <div className="content-div width-50 sm-width-100">
                                    <p className="title-p font-underline">Validation of brand certificates</p>
                                    <p>Brand inspection certificates, except for Change of ownershipand Travel permits shall be automatically cancelled and void one week after time of issuance. This is certificate void if altered.</p>
                                </div>
                                <div className="width-50 width-right d-flex sm-width-100">
                                    <div className="step-btn width-50 sm-width-50 pr-10" onClick={this.handlePrevious}>
                                        <label href="#" className="btn btn-secondary btn-previous"  >Previous <span className="remove-mobile"> &nbsp; step</span></label>
                                    </div>
                                    <div className="step-btn width-50 sm-width-50">
                                        <label href="#" className="btn btn-primary btn-next" onClick={this.handleCheckForm}>Next</label>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <DealerAddPopup display={this.state.dealerAddPopup} onHide={this.handlePopUpForAddDealer} />
            </div>
        );
    }
}
export default LiveStockStep2;