import React from 'react';

const OwnerInfoTag = ({label,value}) => (
    <div className="col-div">
        <div className="label-div">
            <h5>{label}</h5>
        </div>
        <div className="pra-div">
            <p>{value}</p>
        </div>
    </div>
);

export default OwnerInfoTag;