import React from 'react';
const Checkbox = ({id,name,label,checked,onCheckChange}) =>(
    <div className="custom-control custom-checkbox custom-checkbox-new">
        <input 
            type="checkbox"
            checked={checked} 
            onChange={onCheckChange}
            className="custom-control-input" 
            name={name} 
            id={id}/>
        <label 
            className="custom-control-label" 
            htmlFor={id}>
                {label}
        </label>
    </div>
);
export default Checkbox;        